.button,
button,
input[type='submit'] {
  display: inline-block;
  background: #62a4ae;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  font-family: "Overpass", sans-serif;
  font-weight: 700;
  font-weight: 900;
  font-size: 0.9rem;
  white-space: nowrap;
  border: 0px;
  margin-bottom: 10px;
  padding: 15px;
  border-radius: 5px;
  cursor: pointer;
  transition: background 200ms ease-in-out; }
