h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.4;
  margin-top: 20px;
  margin-bottom: 10px;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-family: "Overpass", sans-serif;
  font-weight: 700; }
  h1.not-capped,
  h2.not-capped,
  h3.not-capped,
  h4.not-capped,
  h5.not-capped,
  h6.not-capped {
    text-transform: none;
    letter-spacing: normal;
    font-weight: 700; }

h1 {
  font-size: 28.8px; }
  @media (min-width: 640px) {
    h1 {
      font-size: 48px; } }

h2 {
  font-size: 24px; }
  @media (min-width: 640px) {
    h2 {
      font-size: 40px; } }

h3 {
  font-size: 20.8px; }
  @media (min-width: 640px) {
    h3 {
      font-size: 32px; } }

h4 {
  font-size: 18.2px; }
  @media (min-width: 640px) {
    h4 {
      font-size: 26px; } }

h5 {
  font-size: 16px; }
  @media (min-width: 640px) {
    h5 {
      font-size: 20px; } }

h6 {
  font-size: 14.4px; }
  @media (min-width: 640px) {
    h6 {
      font-size: 16px; } }

p {
  margin-bottom: 10px; }

a {
  text-decoration: none;
  cursor: pointer;
  color: #62a4ae;
  transition: color 200ms ease-in-out; }
  a:hover {
    color: #4b8992; }

em,
i {
  font-style: italic; }

strong,
b {
  font-weight: 700; }

@keyframes fades {
  0% {
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    opacity: 0; } }

.background-gradient {
  background-image: url(../../assets/img/gradientbg.svg);
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat; }
