h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.4;
  margin-top: 20px;
  margin-bottom: 10px;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-family: "Overpass", sans-serif;
  font-weight: 700; }
  h1.not-capped,
  h2.not-capped,
  h3.not-capped,
  h4.not-capped,
  h5.not-capped,
  h6.not-capped {
    text-transform: none;
    letter-spacing: normal;
    font-weight: 700; }

h1 {
  font-size: 28.8px; }
  @media (min-width: 640px) {
    h1 {
