.input,
input,
input[type='submit'] {
  display: block;
  background: #EFEFEF;
  color: #000;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  font-family: "Overpass", sans-serif;
  font-weight: 700;
  font-weight: 900;
  font-size: 0.9rem;
  white-space: nowrap;
  border: 0px;
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: background 200ms ease-in-out; }
  .input:hover,
  input:hover,
  input[type='submit']:hover {
    background: #e2e2e2;
    color: #000; }
  .input.white,
  input.white,
  input[type='submit'].white {
    background: #fff;
    color: #000; }
    .input.white:hover,
    input.white:hover,
    input[type='submit'].white:hover {
      background: #f2f2f2; }
  .input.black,
  input.black,
  input[type='submit'].black {
    background: #222; }
    .input.black:hover,
    input.black:hover,
    input[type='submit'].black:hover {
      background: #2f2f2f; }
  .input.expanded,
  input.expanded,
  input[type='submit'].expanded {
    width: calc(100% - 20px); }
  .input.centered,
  input.centered,
  input[type='submit'].centered {
    display: block;
    margin: 0px auto 10px auto; }
  .input[disabled],
  input[disabled],
  input[type='submit'][disabled] {
    opacity: 0.5;
    cursor: not-allowed;
    pointer-events: none; }

.input-label {
  font-family: "Overpass", sans-serif;
  align-self: start;
  font-weight: 12px;
  text-transform: uppercase;
  font-weight: bold;
  color: #000; }
